.clearfloat:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}
.clearfloat {
  zoom: 1;
  margin: 10px 10px;
}
.clearfloat .right {
  float: right;
}
.clearfloat .right .chat-avatars {
  background: transparent;
  padding: 0;
}
.author-name {
  text-align: center;
  margin: 15px 0 5px 0;
  color: #888;
}
.clearfloat .chat-message {
  max-width: 80%;
  text-align: left;
  padding: 8px 12px;
  word-wrap: break-word;
  display: inline-block;
  position: relative;
}
.clearfloat .left .chat-message {
  background: #fff;
  min-height: 36px;
  border-radius: 0 6px 6px 6px;
}
.clearfloat .left .chat-message:before {
  position: absolute;
  content: "";
  top: 0;
  left: -9px;
  border-top: 0 solid transparent;
  border-bottom: 8px solid transparent;
  border-right: 20px solid #fff;
}
.clearfloat .right {
  text-align: right;
}
.clearfloat .right .chat-message {
  background: #0066ff;
  color: #fff;
  text-align: left;
  min-height: 36px;
  border-radius: 6px 0 6px 6px;
}
.clearfloat .right .chat-message:before {
  position: absolute;
  content: "";
  top: 0;
  right: -9px;
  border-top: 0 solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 20px solid #0066ff;
}
.clearfloat .chat-avatars {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  background: #fff;
  vertical-align: top;
  overflow: hidden;
  padding: 3px 5px;
}
.clearfloat .chat-avatars > img {
  width: 30px;
  height: 34px;
}
.clearfloat .left .chat-avatars {
  margin-right: 10px;
}
.clearfloat .right .chat-avatars .mu-icon {
  color: #b7e0ea;
  font-size: 40px;
}
.clearfloat .right .chat-avatars {
  margin-left: 10px;
}

